import { HttpClient, HttpEventType } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class VimeoService {

  private url: string = 'https://api.vimeo.com/videos'

  constructor(
    private http: HttpClient
  ) {
    const headers = {
      Authorization: `bearer ${ environment.vimeo.token}`
    }
  }

  getWithHeaders(url: string): Observable<any> {
    const headers = {
      Authorization: `bearer ${ environment.vimeo.token}`
    }
    return this.http.get(url, { headers });
  }

  download(id: number): Promise<any> {
    return new Promise<any>((resolve, reject) => {
      this.getWithHeaders(`${ id }`).subscribe((event: any) => {
        if (event.type === HttpEventType.DownloadProgress) {
          const progress = Math.round(100 * event.loaded / event.total);
          console.log(`Downloading ${ progress} ...`);
        } else if (event.type === HttpEventType.Response) {
          const blob = new Blob([event.body]);
          console.log(`Downloaded ${ blob } ...`);
          alert('donloaded');
          resolve(blob);
        }
      }, err => {
        console.log(err);
        reject(err)
      });
    });
  }

  getAndDownload(id: number): Promise<any> {
    return new Promise((resolve, reject) => {
      this.getWithHeaders(`${ this.url }/${ id }`)
        .subscribe(async (event: any) => {
          const files = event.download.sort((a, b) => a.size - b.size)
          console.log(files);        
          const blob = await this.download(files[files.length - 1].link);
      }, err => {
        console.log(err);
        reject(err)
      });
    })
  }
}
